<template>
	<v-card>
		<v-card-title>
			Cosponsors (total: {{items.length}})
			<v-spacer></v-spacer>
			<v-text-field
					v-model="search"
					append-icon="mdi-magnify"
					label="Search"
					hide-details
					class="mr-5"
					clearable
			></v-text-field>
			<v-text-field
					:value="itemsPerPage"
					label="Items per page"
					type="number"
					hide-details
					min="-1"
					max="15"
					@input="itemsPerPage = parseInt($event, 10)"
			></v-text-field>
		</v-card-title>
		<v-data-table
				dense
				:headers="headers"
				:items="items"
				:search="search"
				:page.sync="page"
				:items-per-page="itemsPerPage"
				hide-default-footer
				class="elevation-1 ma-10"
				@page-count="pageCount = $event"
		>
			<template v-slot:item.supports="{ item }">
				<v-checkbox v-model="item.supports" @change="putSupports(item)"></v-checkbox>
			</template>
			<template v-slot:item.isProfane="{ item }">
				<v-checkbox v-model="item.isProfane" @change="putProfane(item)"></v-checkbox>
			</template>
		</v-data-table>
		<div class="text-center pt-2">
			<v-pagination v-model="page" :length="pageCount"></v-pagination>
		</div>
		<v-snackbar v-model="snackbar.show" top>
			{{snackbar.text}}
		</v-snackbar>
	</v-card>
</template>

<script>
	export default {
		data () {
			return {
				search: '',
				snackbar: {
					show: false,
					text: '',
				},
				page: 1,
				pageCount: 0,
				itemsPerPage: 500,
				headers: [
					{ text: 'id', value: 'id' },
					{ text: 'supports', value: 'supports' },
					{ text: 'flagged', value: 'isProfane' },
					{ text: 'name', value: 'name' },
					{ text: 'email', value: 'email' },
					{ text: 'zipcode', value: 'zipcode' },
					{ text: 'geo', value: 'geo.city' },
					{ text: 'submissionScore', value: 'submissionScore' },
					{ text: 'created at', value: 'createdAt' },
				],
				items: [],
			}
		},
		methods: {
			async getAll() {
				let response = await this.$http.get(`${process.env.VUE_APP_API}/supporters/report?adminKey=${this.$route.query.adminKey}`)
				this.items = response.data
			},
			async putSupports(item) {
				await this.$http.put(`${process.env.VUE_APP_API}/supporters?adminKey=${this.$route.query.adminKey}`, {
					id: item.id,
					supports: item.supports
				})
				this.snackbar.text = `${item.name} updated`
				this.snackbar.show = true
			},
			async putProfane(item) {
				await this.$http.put(`${process.env.VUE_APP_API}/supporters?adminKey=${this.$route.query.adminKey}`, {
					id: item.id,
					isProfane: item.isProfane
				})
				this.snackbar.text = `${item.name} updated`
				this.snackbar.show = true
			}
		},
		created() {
			this.getAll()
		}
	}
</script>

