        <v-row>
          <v-col cols="12">
            <h2 class="text-center">What would the George Floyd Justice in Policing Act do?</h2>
          </v-col>
          <v-col cols="12" sm="6">
            <v-expansion-panels v-model="panel1">
              <v-expansion-panel
              >
                <v-expansion-panel-header>Work to End Racial Profiling</v-expansion-panel-header>
                <v-expansion-panel-content>
                  The Justice in Policing Act works to end racial profiling by explicitly prohibiting such profiling in law and requiring all law enforcement officers to participate in training to recognize bias and discriminatory practices. It also requires the collection of data on all investigatory activities in order to ensure that patterns of racial profiling can be identified and rooted out.              </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Ban Police Chokeholds</v-expansion-panel-header>
                <v-expansion-panel-content>
                  The Justice in Policing Act bans chokeholds and carotid holds for all federal law enforcement officers. It also conditions all federal funding for state and local governments on implementing this ban on their own law enforcement agencies. The bill would also require that deadly force be used as a last resort only, requiring that officers first make use of de-escalation techniques. It would also change the standard used to evaluate whether officers’ use of force was justified, moving from a standard of “reasonable” to one of “necessary.” Federal funds to state and local governments would also be conditioned on adoption of this “necessary” standard for use of force.              </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Ban No-Knock Warrants</v-expansion-panel-header>
                <v-expansion-panel-content>
                  Under the Justice in Policing Act, “no-knock warrants,” like the one that led to the death of Breonna Taylor in her own home, would be made illegal at the federal level, with federal funds to state and local authorities conditioned on banning the practice.              </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Limit Military Equipment on American Streets</v-expansion-panel-header>
                <v-expansion-panel-content>
                  If passed, the Justice in Policing Act would limit the transfer of military-grade equipment from the Pentagon to state and local law enforcement agencies. Outside of special circumstances necessitated by clear and extreme threats to public safety, there is no reason for local law enforcement to be outfitted with heavy-duty military equipment. The presence of armored vehicles and military-grade weapons alienates residents from those meant to be keeping their communities safe and peaceful.              </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Community & Public Safety</v-expansion-panel-header>
                <v-expansion-panel-content>
                  The Justice in Policing Act would establish public safety innovation grants, which would support community-based organizations working to create local commissions and task forces that help communities reimagine and develop more just and more equitable approaches to public safety.  These commissions and task forces would aim to achieve at the local level what the Obama Administration’s Task Force on 21st Century Policing has done at the national level.  They would also help empower communities to promote changes to the culture of local law enforcement agencies.
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-col>
          <v-col cols="12" sm="6">
            <v-expansion-panels v-model="panel2">
              <v-expansion-panel
              >
                <v-expansion-panel-header>Require All Officers to Wear Body Cameras</v-expansion-panel-header>
                <v-expansion-panel-content>
                  The Justice in Policing Act would require all federal uniformed police officers to wear body cameras and would condition federal funds for state and local governments on their officers’ use of body cameras as well. It would also require that federal police vehicles are all equipped with dashboard cameras to ensure that police encounters are recorded to protect the civil rights of involved.              </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Hold Police Accountable in Court</v-expansion-panel-header>
                <v-expansion-panel-content>
                  The Justice in Policing Act would amend the law that currently limits the authority of prosecutors to hold police officers accountable for their misconduct. It would do so by changing the mens rea requirement in 18 U.S.C. Section 242 to a standard of “recklessness” instead of “willfulness.” The bill would also eliminate qualified immunity for law enforcement personnel, making it possible for individuals to sue for civil damages when their constitutional and civil rights have been violated.              </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Investigate Systemic Police Misconduct</v-expansion-panel-header>
                <v-expansion-panel-content>
                  It is critical that the federal Justice Department be fully empowered to investigate systemic misconduct in police departments, which is why the Justice in Policing Act grants the Justice Department’s Civil Rights Division power to subpoena and creates a grant program for state attorneys-general to develop authority to conduct independent inquiries and investigate police departments where there is evidence of a pattern of misconduct.              </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Changing the Culture of Law Enforcement</v-expansion-panel-header>
                <v-expansion-panel-content>
                  Recognizing the need to change the culture of law enforcement in order to root out racial bias, the Justice in Policing Act would create development and training programs to promote best practices and would expand funding for pattern and practice discrimination investigations and programs managed by the Justice Department’s Community Relations Service.  It would also require the creation of law enforcement accreditation standards based on the recommendations issued by the Obama Administration’s Task Force on 21st Century Policing.  Moreover, it would authorize a study of the impact of laws and rules that allow officers to delay answering questions posed by investigators of misconduct.  The bill would additionally establish a Department of Justice Task Force to coordinate the investigation, prosecution, and enforcement of federal, state, and local governments in cases relating to police misconduct.  Furthermore, it would require the Justice Department to collect data nationwide on investigatory actions and detentions by federal law enforcement agencies, the racial distribution of drug charges, traffic and pedestrian stops and detentions, and the use of deadly force by and against law enforcement officers.                </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel
              >
                <v-expansion-panel-header>Additional Provisions</v-expansion-panel-header>
                <v-expansion-panel-content>
                  <ul>
                    <li>Require law enforcement accreditation standards to comply with recommendations from the Obama Administration’s Task Force on 21st Century Policing;
                    </li>
                    <li>Enhance funding for investigations into pattern and practice discrimination;</li>
                    <li>Expand funding for programs managed by the Department of Justice’s Community Relations Service;</li>
                    <li>Study the impact of laws or rules allowing officers to delay providing answers to investigators during inquiries into misconduct;</li>
                    <li>Establish public safety innovation grants for community-based organizations to launch local commissions and task forces modeled after the Obama Administration’s Task Force on 21st Century Policing, which would help communities re-imagine just and equitable approaches to public safety;</li>
                    <li>Support community-based programs to help change police culture and promote dialogue about how to develop more equitable and more just community policing;</li>
                    <li>Require the U.S. Attorney General to collect data from federal law enforcement agencies on investigations and detentions, on the racial distribution of drug charges, on the use of deadly force by and against law enforcement officers, and on traffic and pedestrian stops and detentions;</li>
                    <li>Establish a Justice Department task force to coordinate the investigation, prosecution, and enforcement efforts of federal, state, and local governments in cases relating to police misconduct;</li>
                    <li>Create a national police misconduct registry to ensure that problematic officers who are dismissed from one agency cannot simply move to an agency in another jurisdiction without being held accountable;</li>
                    <li>Mandate state and local law enforcement agencies to report use-of-force data and include statistics on race, sex, disability, religion, and age of those involved in police use of force;</li>
                  </ul>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-col>
          <v-col cols="12">
