              v-if="commentForm.deliverToCongress"
              :rules="requiredRules"
              :required="commentForm.deliverToCongress"
              filled
              label="Your Address"></v-text-field>
            </VueGooglePlaces>
          <v-text-field
              v-model="email"
              v-if="commentForm.deliverToCongress"
              :required="commentForm.deliverToCongress"
              :rules="emailRules"
              filled
              label="Email"></v-text-field>
          <v-divider></v-divider>
          <p class="font-weight-thin mt-5">Comments must be relevant to the bill and cannot be illegal, obscene, profane, defamatory, threatening, infringing of intellectual property rights, invasive of privacy, or campaign soliciting. Visitors may flag any comments that they believe violate this policy, and the Majority Leader's Office reserves the right to remove comments that violate this policy. Report any bugs or issues to <a mailto="DomeWatch@mail.house.gov">DomeWatch@mail.house.gov</a></p>
          <v-banner clas="mb-5" single-line v-if="commentError.show === true">
            <v-icon
                slot="icon"
                color="warning"
                size="36"
            >
              error
            </v-icon>
            {{ commentError.text }}
          </v-banner>
          <v-card-actions>
            <v-btn text @click="commentDialog=false">Cancel</v-btn>
            <v-spacer></v-spacer>
            <v-btn
                color="error"
                :loading="commentFormLoading"
                :disabled="!commentValid"
                @click="commentSubmit()">Submit public comment</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar.show">{{snackbar.text}}</v-snackbar>
  </div>
</template>

<script>
import VueGooglePlaces from 'vue-google-places'
Vue.use(VueGooglePlaces)
import Vue from 'vue';
const dayjs = require('dayjs')
import relativeTime from 'dayjs/plugin/relativeTime'
dayjs.extend(relativeTime)
import { VueReCaptcha } from 'vue-recaptcha-v3';
Vue.use(VueReCaptcha, { siteKey: '6LeSLqUZAAAAAFH3qdzPtcrMyMNnL-bgcxQuuizs' });

export default {
  name: 'Home',
  components: {
    Votes: () => import('../components/Votes'),
    twitterbutton: () => import('vue-share-buttons/src/components/TwitterButton'),
    facebookbutton: () => import('vue-share-buttons/src/components/FacebookButton'),
    CosponsorsMap: () => import('../components/CosponsorsMapHome'),
    CommentImpression: () => import('../components/CommentImpression')
  },
  data: () => ({
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
    ],
    commentError: {
      show: false,
      text: ''
    },
    cosponsorError: {
      show: false,
      text: ''
    },
    nameRules: [
      v => !!v || 'Name is required',
    ],
    requiredRules: [
      v => !!v || 'This field is required',
    ],
    notNullRules: [
      v => v !== null || 'This field is required',
    ],
    zipRules: [
      v => !!v || 'Zipcode is required',
      v => (v && v.length === 5) || 'Zipcode must be 5 digits',
    ],
    sentiment: null,
    panel1: [0, 1],
    panel2: [0, 1],
    readMore: false,
    comments: [],
    commentCheckbox: ['Work to End Racial Profiling', 'Banning Chokeholds', 'Banning No-Knock Warrants', 'Limiting Military Equipment on American Streets', 'Requiring Police Body Cameras', 'Holding Police Accountable in Court', 'Investigating Systemic Police Misconduct', 'Other Provisions'],
    commentDialog: false,
    commentShareDialog: false,
    commentValid: false,
    commentForm: {
      deliverToCongress: false,
      public: true,
      body: '',
      billSection: null,
    },
    commentFormLoading: false,
    cosponsors: [],
    cosponsorsCount: null,
    cosponsorDialog: false,
    cosponsorFormLoading: false,
    cosponsorValid: true,
    cosponsorSubmitted: false,
    opposedCount: null,
    name: null,
    radios: null,
    zipcode: null,
    email: null,
    timer: '',
    commentTimer: '',
    snackbar: {
      show: false,
      text: null
    }
  }),
  methods: {
    relativeTime(date) {
      return dayjs(date).fromNow()
    },
    async commentsGet() {
      let result
      if (this.$route.query.adminKey) {
        result = await this.$http.get(this.$api + '/comments/admin?adminKey=' + this.$route.query.adminKey)
      } else {
        result = await this.$http.get(this.$api + '/comments?limit=25&type=' + this.commentCheckbox)
      }
      this.comments = result.data
    },
    async commentSubmit() {
      this.commentFormLoading = true
      try {
        if (!this.$refs.commentForm.validate()) return
        this.commentForm.name = this.name
        this.commentForm.email = this.email
        this.commentForm.zipcode = this.zipcode
        this.commentForm.captcha  = await this.reCaptcha()
        await this.$http.post(this.$api + '/comments', this.commentForm)
        this.commentDialog = false
        this.snackbar.text = 'Your comment has been submitted'
        this.snackbar.show = true
      } catch (e) {
        this.commentError.text = e.response.data
        this.commentError.show = true
        console.error(e)
      } finally {
        this.commentFormLoading = false
        await this.commentsGet()
      }
    },
    async cosponsorSubmit() {
      this.cosponsorFormLoading = true
      try {
        if (!this.$refs.form.validate()) return
        this.cosponsorForm = {
          name: this.name,
          zipcode: this.zipcode,
          email: this.email,
          supports: this.radios,
          captcha: null
        }
        this.cosponsorForm.captcha  = await this.reCaptcha()
        await this.$http.post(this.$api + '/supporters', this.cosponsorForm)
        this.cosponsorSubmitted = true
        this.cosponsorDialog = true
        await this.cosponsorsGet()
      } catch (e) {
        this.cosponsorError.text = e.response.data
        this.cosponsorError.show = true
        console.error(e)
      } finally {
        this.cosponsorFormLoading = false
      }
    },
    async cosponsorsGet() {
      this.getCount()
      let result = await this.$http.get(this.$api + '/supporters?limit=6')
      this.cosponsors = result.data
    },
    async getCount() {
      let result = await this.$http.get(this.$api + '/supporters/count')
      let oresult = await this.$http.get(this.$api + '/supporters/count?supports=false')
      this.cosponsorsCount = result.data
      this.opposedCount = oresult.data
    },
    async reCaptcha(){
      try{
          await this.$recaptchaLoaded();
          return await this.$recaptcha('login')
      } catch (e) {
        console.log(`#reCaptcha: ${e}`);
      }
    }
  },
  async created() {
    this.cosponsorsGet()
    this.commentsGet()
    this.timer = setInterval(this.cosponsorsGet, 10000)
  },
  beforeDestroy () {
    clearInterval(this.timer)
    clearInterval(this.commentTimer)
  }
}
</script>

