import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Supporters from '../views/Supporters.vue'
import AdminCosponsorsPage from "../views/Admin/AdminCosponsorsPage"

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    component: Home
  },
  {
    path: '/cosponsors',
    component: Supporters
  },
  {
    path: '/admin/cosponsors',
    component: AdminCosponsorsPage,
    meta: {admin: true}
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
