an on their own law enforcement agencies. The bill would also require that deadly force be used as a last resort only, requiring that officers first make use of de-escalation techniques. It would also change the standard used to evaluate whether officers’ use of force was justified, moving from a standard of “reasonable” to one of “necessary.” Federal funds to state and local governments would also be conditioned on adoption of this “necessary” standard for use of force. ")])],1),_c('v-expansion-panel',[_c('v-expansion-panel-header',[_vm._v("Ban No-Knock Warrants")]),_c('v-expansion-panel-content',[_vm._v(" Under the Justice in Policing Act, “no-knock warrants,” like the one that led to the death of Breonna Taylor in her own home, would be made illegal at the federal level, with federal funds to state and local authorities conditioned on banning the practice. ")])],1),_c('v-expansion-panel',[_c('v-expansion-panel-header',[_vm._v("Limit Military Equipment on American Streets")]),_c('v-expansion-panel-content',[_vm._v(" If passed, the Justice in Policing Act would limit the transfer of military-grade equipment from the Pentagon to state and local law enforcement agencies. Outside of special circumstances necessitated by clear and extreme threats to public safety, there is no reason for local law enforcement to be outfitted with heavy-duty military equipment. The presence of armored vehicles and military-grade weapons alienates residents from those meant to be keeping their communities safe and peaceful. ")])],1),_c('v-expansion-panel',[_c('v-expansion-panel-header',[_vm._v("Community & Public Safety")]),_c('v-expansion-panel-content',[_vm._v(" The Justice in Policing Act would establish public safety innovation grants, which would support community-based organizations working to create local commissions and task forces that help communities reimagine and develop more just and more equitable approaches to public safety. These commissions and task forces would aim to ac