<template>
	<div id="cosponsors-map">
		<v-row>
			<v-col cols="12">
				<h2>Cosponsors by State</h2>
				<GChart type="GeoChart" :settings="{ packages: [ 'geochart'], mapsApiKey: apiKey, animation: {startup: true}  }" :data="chartData"
								:options="chartOptions" />
			</v-col>

			<v-col cols="12">
				<h2>Cosponsors by City</h2>
				<GChart type="GeoChart" :settings="{ packages: [ 'geochart'], mapsApiKey: apiKey  }" :data="pinMapData"
								:options="pinMapOptions" />
			</v-col>
		</v-row>
	</div>
</template>
<script>
	export default {
		name: "cosponsors-map",
		components: {

		},
		data() {
			return {
				chartData: [
					['State', 'Cosponsors']
				],
				chartOptions: {
					region: 'US',
					resolution: 'provinces',
					displayMode: 'regions',
					backgroundColor: 'transparent',
					defaultColor: '#efefef',
					colorAxis: {minValue: 0, values: [0, 1, 10, 50], colors: ['#f9f9f9', '#f5ab34', '#ff6d0c', '#cf0a05']},
					height: '425',
					width: '600'
				},
				pinMapOptions: {
					region: 'US',
					displayMode: 'markers',
					resolution: 'provinces',
					backgroundColor: 'transparent',
					legend: 'none',
					sizeAxis: { minValue: 0, maxValue: 100 },
					colorAxis: {minValue: 0, colors: ['#b71d1c', '#b71d1c']},
					height: '425',
					width: '600'
				},
				pinMapData: [
					['Lat','Lng','City', 'Cosponsors'],
					[38.9024,-76.9901,'Washington', 42],
				],
				apiKey: 'AIzaSyBjISgqRC4F4J2IBB8bQE6nJry2_r576ek'
			}
		},
		methods: {
			async getCosponsorMapData(){
				var cosponsorObj = await this.$http.get(this.$api + '/supporters/sponsorTotalByState');
				var cosponsorArray = cosponsorObj.data.cosponsorDetails;

				cosponsorArray.forEach((cosponsor) => {
					this.chartData.push(Object.values(cosponsor));
				});
			},
			async getCosponsorPinMapData(){
				let cosponsorObj = await this.$http.get(this.$api + '/supporters/sponsorTotalByCity');
				var cosponsorArray = cosponsorObj.data;

				cosponsorArray.forEach((cosponsor) => {
					this.pinMapData.push(Object.values(cosponsor));
				});
			}
		},
		async mounted(){
			await this.getCosponsorMapData();
			await this.getCosponsorPinMapData();
		},
	}
</script>
