<template>
  <div id="cosponsors" style="background-color:black;height:100%">
    <v-container>
      <v-toolbar flat color="black" dark class="px-0 mx-0">
        <div class="d-flex align-center">
          <v-img
            alt="Justice in Policing logo"
            class="shrink mr-1"
            contain
            :src="require('./bug.png')"
            width="70"
            transition="true"
          />
        </div>
        <v-spacer></v-spacer>
        <v-btn href="#" color="error" class="mr-10 d-none d-sm-flex" v-scroll-to="'#cos'">
          <span class="mr-2">Become a Citizen Cosponsor</span>
        </v-btn>
        <v-btn
          target="_blank"
          color="#f5ab34"
          class="d-none d-sm-flex"
          light
          v-scroll-to="'#comment'"
        >
          <span class="mr-2">SHARE A COMMENT OR STORY</span>
        </v-btn>
      </v-toolbar>
      <v-row class="pt-5 my-12">
        <v-col cols="12" md="6">
          <cosponsorsMap />
        </v-col>
        <v-col cols="12" md="6">
          <div>
            <h2
                class="white--text text-center pb-5"
                style="margin:0 auto"
            >{{count}} Cosponsors since June 24, 2020</h2>
            <v-col cols="12">
              <v-row align="center" justify="center">
                <v-list class="pa-5">
                  <v-list-item v-for="item in cosponsors" :key="item.id">
                    <v-list-item-avatar color="grey">
                      <img
                          v-if="item.geo && item.geo.state"
                          style="filter: invert(1);"
                          :src="`https://raw.githubusercontent.com/MajorityLeader/state-svg-defs/master/SVG/${item.geo.state}.svg`"
                      />
                    </v-list-item-avatar>
                    {{item.name}} cosponsored {{relativeTime(item.createdAt) }} from {{item.geo.city}}, {{item.geo.state}}
                  </v-list-item>
                  <v-btn block @click="offset += 100; limit += 100; cosponsorsGet()">Load More</v-btn>
                </v-list>
              </v-row>
            </v-col>
          </div>
          <div v-if="opposed">
            <h2
                class="white--text text-center py-5"
                style="margin:0 auto"
            >{{opposed.length}} Opposed since June 24, 2020</h2>
            <v-col cols="12">
              <v-row align="center" justify="center">
                <v-list class="pa-5">
                  <v-list-item v-for="item in opposed" :key="item.id">
                    <v-list-item-avatar color="grey">
                      <img
                          v-if="item.geo && item.geo.state"
                          style="filter: invert(1);"
                          :src="`https://raw.githubusercontent.com/MajorityLeader/state-svg-defs/master/SVG/${item.geo.state}.svg`"
                      />
                    </v-list-item-avatar>
                    {{item.name}} cosponsored {{relativeTime(item.createdAt) }} from {{item.geo.city}}, {{item.geo.state}}
                  </v-list-item>
                </v-list>
              </v-row>
            </v-col>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
const dayjs = require("dayjs");
import relativeTime from "dayjs/plugin/relativeTime";
dayjs.extend(relativeTime);
import cosponsorsMap from "../components/CosponsorsMap.vue";

export default {
  name: "Supporters",
  components: {
    cosponsorsMap
  },
  data: () => ({
    cosponsors: [],
    opposed: null,
    count: null,
    offset: 0,
    limit: 40
  }),
  methods: {
    async cosponsorsGet() {
      await this.getCount();
      let result = await this.$http.get(
        this.$api + "/supporters?limit=" + this.limit + "&offset=" + this.offset
      );
      this.cosponsors = result.data;
    },
    async opposedGet() {
      await this.getCount();
      let result = await this.$http.get(
        this.$api + "/supporters?supports=false&limit=500"
      );
      this.opposed = result.data;
    },
    async getCount() {
      let result = await this.$http.get(this.$api + "/supporters/count");
      this.count = result.data;
    },
    relativeTime(date) {
      return dayjs(date).fromNow();
    }
  },
  created() {
    this.cosponsorsGet();
    this.opposedGet();
  }
};
</script>

